:root {
  --bodybg: #dcdefe;
  --primary-color: #5138ee;
  --grey: #d6d6d6;
  --placeholder: #969696;
  --white: #fff;
  --text: #333;
  --slider-bg: #eff3ff;
  --login-cta-hover: #1f0098;
}

* {
  margin: 0;
  padding: 0;
}

#root {
  background-color: #dcdefe;
  background-color: var(--bodybg);
  background-image: url(/static/media/background-login.cbf13075.jpeg);
  background-blend-mode: darken;
  background-size: cover;
background-repeat: no-repeat;
background-position: center;
  font-family: "Raleway", sans-serif;
  height: 100vh;
  display: flex;
}

.login-container {
  display: flex;
  max-width: 1200px;
  background: #fff;
  background: var(--white);
  margin: auto;
  width: 100%;
  min-width: 320px;
  overflow: hidden;
    border-radius: 20px;
}

.login-container .logo svg {
  height: 40px;
  width: 40px;
  fill: #5138ee;
  fill: var(--primary-color);
}

.login-container .login-form {
  box-sizing: border-box;
  padding: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 1;
}

.login-container .login-form .login-form-inner {
  max-width: 380px;
  width: 95%;
}



.login-container .login-form .sign-in-seperator {
  text-align: center;
  color: #969696;
  color: var(--placeholder);
  position: relative;
  margin: 30px 0 20px;
}

.login-container .login-form .sign-in-seperator span {
  background: #fff;
  background: var(--white);
  z-index: 1;
  position: relative;
  padding: 0 10px;
  font-size: 14px;
}

.login-container .login-form .sign-in-seperator:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #d6d6d6;
  background: var(--grey);
  left: 0;
  top: 50%;
  z-index: 0;
}

.login-container .login-form .login-form-group {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.login-container .login-form .login-form-group input {
  padding: 13px 20px;
  box-sizing: border-box;
  border: 1px solid #d6d6d6;
  border: 1px solid var(--grey);
  border-radius: 50px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #333;
  color: var(--text);
  transition: linear 0.2s;
}



.login-container
  .login-form
  .login-form-group
  input::-webkit-input-placeholder {
  color: #969696;
  color: var(--placeholder);
  font-weight: 300;
  font-size: 14px;
}

.login-container .login-form .login-form-group.single-row {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 5px;
}




.login-container .login-form .link {
  color: #5138ee;
  color: var(--primary-color);
  font-weight: 700;
  text-decoration: none;
  font-size: 14px;
}

.login-container .login-form .link:hover {
  text-decoration: underline;
}

.login-container .login-form .login-cta {
  color: #fff;
  color: var(--white);
  text-decoration: none;
  margin: 25px 0 35px;
  background: #5138ee;
  background: var(--primary-color);
}

.login-container .login-form .login-cta:hover {
  background: #1f0098;
  background: var(--login-cta-hover);
}

.login-container .onboarding {
  flex: 1 1;
  background: #eff3ff;
  background: var(--slider-bg);
  display: none;
  width: 50%;
}

.login-container .login-form .login-form-group label .required-star {
  color: #5138ee;
  color: var(--primary-color);
  font-size: 18px;
  line-height: 10px;
}

.login-container .rounded-button {
  display: flex;
  width: 100%;
  text-decoration: none;
  border-radius: 50px;
  padding: 13px 20px;
  box-sizing: border-box;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  align-items: center;
  transition: linear 0.2s;
}

.login-container .rounded-button:hover {
  box-shadow: 0px 0px 4px 0px #d6d6d6;
  box-shadow: 0px 0px 4px 0px var(--grey);
}

.login-container .body-text {
  font-size: 14px;
  font-weight: 500;
  color: #333;
  color: var(--text);
}

.login-container .onboarding .swiper-container {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-container .onboarding .swiper-slide {
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  color: #333;
  color: var(--text);
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;

}

.login-container .onboarding .swiper-pagination-bullet-active {
  background-color: #5138ee;
  background-color: var(--primary-color);
}

.login-container .onboarding .swiper-slide {
  flex-direction: column;
  display: flex;
  position: relative;
}

.login-container .onboarding .swiper-slide .slide-image img {
  width: 60%;
  height: 80%;
}
.slide-image::after{
  content: '';
  width: 100%;
  height: 46px;
  background-image: url(/static/media/logo-view.c170f677.png);
  position: absolute;
  left: 0;
  background-size: 60%;
  background-position: center bottom;
  background-repeat: no-repeat;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  bottom: -16%;
  opacity: 0.1;
}
.login-container .onboarding .slide-content {
  width: 60%;
}

.login-container .onboarding .slide-content h2 {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 15px;
}

.login-container .onboarding .slide-content p {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
}
.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 30px;
}

.login-container .login-form .login-form-inner h1 {
  margin-bottom: 20px;
  margin-top: 10px;
  text-align: center;
  color: #007e89;
}

@media screen and (min-width: 768px) {
  .login-container .onboarding {
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .login-container {
    height: 100vh;
  }
}

@media screen and (width: 768px) {
  .login-container .onboarding {
    order: 0;
  }
  .login-container .login-form {
    order: 1;
  }
  .login-container {
    height: 100vh;
  }
}

@media screen and (max-width: 420px) {
  .login-container .login-form {
    padding: 20px;
  }
  .login-container .login-form-group {
    margin-bottom: 16px;
  }
  .login-container {
    margin: 0;
  }
}
.ant-legacy-form.ant-legacy-form-horizontal.login-form{
  padding: 25px;
}
